import './scss/index.scss';
import Parallax from './parallax.js'
import Typewriter from '../node_modules/typewriter-effect/dist/core'
import alertify from '../node_modules/alertifyjs/build/alertify'

// Parallaxe
let scene = document.getElementById('scene');
let parallaxInstance = new Parallax(scene, {
    relativeInput: true
});
parallaxInstance.friction(0.05, 0.05);
parallaxInstance.limit(40, 20);

// Typewriter
new Typewriter('#typewriter', {
    strings: ["Maonnerie gnrale", "Constructions neuves", "Rnovation de l'existant", "Btiments agricoles", "Fosses & Assainissement"],
    autoStart: true,
    loop: true,
    delay: 100,
    deleteSpeed: 35,
    pauseFor: 6000,
});

// AJAX Form

document.querySelector("form").addEventListener("submit", handleSubmit);

const handleSubmit = (e) => {
    e.preventDefault()
    let myForm = document.getElementById('submit-contact');
    let formData = new FormData(myForm)
    fetch('/', {
        method: 'POST',
        headers: { "Content-Type": "application/x-www-form-urlencoded" },
        body: new URLSearchParams(formData).toString()
    }).then(() => {
        alertify.success('Message envoy. Nous vous recontacterons au plus tt !');
        console.log('Formulaire envoy !');
    }).catch((error) => {
        alertify.success("Erreur lors de l'envoi du message. Veuillez ressayer.");
        alert(error);
    })
}